<template>
  <b-container>
    <div class="page-header">
      <h2 class="text-center"> Gestore Netflix </h2>
      <hr />
    </div>
    <b-row>
      <b-col lg="4">
        <b-form v-on:submit.prevent="onSubmit()">

          <b-form-group label="Persona" label-for="persona">
            <b-form-input type="text" id="persona" name="persona" v-model="newGame.Persona" v-on:change="onChange()" />
            </b-form-group>

            <b-form-group label="Periodo" label-for="periodo">
            <b-form-input type="text" id="periodo" name="periodo" v-model="newGame.Periodo" v-on:change="onChange()" />
            </b-form-group>

            <b-form-group label="Anno" label-for="anno">
            <b-form-select id="anno" name="anno" v-model="newGame.Anno" :options="gameTypes">
            </b-form-select>
            </b-form-group>

            <b-form-group label="Pagamento" label-for="pagamento">
            <b-form-select id="pagamento" name="pagamento" v-model="newGame.Pagamento" :options="gamePlatforms">
            </b-form-select>
            </b-form-group>

            <b-btn type="submit" variant="primary" :disabled="submitIsDisabled">Aggiungi</b-btn>
        </b-form>
        <br />
      </b-col>

      <b-col lg="8">
        <b-table striped hover :items="games" :fields="fields">
          <template v-slot:cell(Anno)="data">{{getGameType(data.item.Anno)}}</template>
          <template v-slot:cell(Pagamento)="data">
            <img :src="getPlatformImage(data.item.Pagamento)" width="32">
          </template>
          <!-- eslint-disable-next-line -->
          <template v-slot:cell(Operazioni)="data">
            <b-btn size="sm" variant="warning" @click="onDelete(data.item)"> X </b-btn>&nbsp;
            <b-btn size="sm" variant="secondary" @click="onEdit(data.item)" v-b-modal.editGameModal> M </b-btn>
          </template>
        </b-table>
      </b-col>
    </b-row>

    <b-modal id="editGameModal" title="Modifica Game" @ok="onUpdate()">
      <b-form-group label="Persona" label-for="persona">
        <b-form-input type="text" id="titolo" name="titolo" v-model="editGame.Persona" />
      </b-form-group>

      <b-form-group label="Periodo" label-for="periodo">
        <b-form-input type="text" id="softwarehouse" name="softwarehouse" v-model="editGame.Periodo" />
      </b-form-group>

      <b-form-group label="Anno" label-for="anno">
        <b-form-select id="tipo" name="tipo" v-model="editGame.Anno" :options="gameTypes">
        </b-form-select>
      </b-form-group>

       <b-form-group label="Pagamento" label-for="pagamento">
        <b-form-select id="piattaforma" name="piattaforma" v-model="editGame.Pagamento" :options="gamePlatforms">
        </b-form-select>
      </b-form-group>
    </b-modal>
  </b-container>
</template>

<script>
import { gamesRef } from './firebase'
import { GameTypeEnum, PiattaformaEnum } from './models/game'

export default {
  firebase: {
    games: gamesRef.orderByChild('Persona')
  },
  data(){
    return {
      gameTypes: GameTypeEnum.properties,
      gamePlatforms: PiattaformaEnum.properties,
      fields: ['Persona', 'Periodo', 'Anno', 'Pagamento', 'Operazioni'],
      games:[],
      newGame:{
        Persona:'',
        Periodo: '',
        Anno: GameTypeEnum.FPS,
        Pagamento: PiattaformaEnum.No
      },
      editGame: {},
      originKey: '',
      submitIsDisabled: true
    }
  },
  methods: {
    getPiattaforma(value) {
      return this.gamePlatforms[value].text;
    },
    getGameType(value) {
      return this.gameTypes[value].text;
    },
    onSubmit() {
      gamesRef.push(this.newGame);
      this.newGame.Persona = '';
      this.newGame.Periodo = '';
      this.submitIsDisabled = true;
    },
    onChange() {
      this.submitIsDisabled = this.newGame.Persona === '' || this.newGame.Periodo === '';
    },
    onDelete(game) {
      gamesRef.child(game['.key']).remove();
    },
    onEdit(game) {
      this.originKey = game['.key'];
      this.editGame = {
        Persona: game.Persona,
        Periodo: game.Periodo,
        Anno: game.Anno,
        Pagamento: game.Pagamento
      }
    },
    onUpdate() {
      gamesRef.child(this.originKey).update(this.editGame);
    },
    getPlatformImage(value) {
      return require('./assets/p_' + value + '.png');
    }
  }
}
</script>

<style lang="scss">
.page-header {
  color: #E50914;
}
.page-header h2 {
  padding-top: 8px;
}
</style>
